.all-products {
    .load-more {
        display: flex;
        justify-content: center;
        margin-top: 8px;
        margin-bottom: 50px;
        button {
            background-color: transparent;
            border: 1.5px solid var(--yellow-color);
            border-radius: 5px;
            padding: 15px;
            cursor: pointer;
        }
    }

    .items {
        display: grid;
        grid-template-columns: repeat(3, 32%);
        justify-content: center;
        gap: 20px;
        padding: 10px;
    }
    .product-card {
        border: 1px solid var(--yellow-color-trans);
        padding: 15px;

        a {
            color: black;
            text-decoration: none;
            text-transform: uppercase;
            font-weight: 600;
        }

        img {
            max-width: 60%;
            max-height: 100%;
        }

        .image {
            height: 300px;
            margin-bottom: 15px;
        }
    }

    .title {
        /* cut overflowing text */
        height: 40px;
        line-height: 20px;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        text-align: center;
    }

    .divider {
        margin: 0 -15px 15px;
        padding-bottom: 15px;
        border-bottom: 1px solid var(--yellow-color-trans);
    }

    .item-menu {
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 30px;
        background-color: blanchedalmond;

        a {
            &:hover {
                background-color: rgba(0, 0, 0, 0.05);
            }

            &.active {
                background-color: rgb(255, 222, 173);
            }
        }
    }

    .category {
        width: 100%;
        height: 100%;
        border: 1px solid;
        list-style: none;
        text-decoration: none;
        color: black;
        text-transform: capitalize;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .product-card-actions {
        display: grid;
        grid-template-columns: 1fr 2fr;
        align-items: center;
    }

    .add-to-cart {
        display: grid;
        grid-template-columns: 1fr 1fr;
        justify-items: center;
        height: 40px;

        button {
            cursor: pointer;
        }
    }

    .add-to-cart-button {
        justify-self: end;

        button {
            background-color: rgb(238, 173, 53);
            border-radius: 20px;
            border: 0;
            padding: 12px 15px;
        }
    }
}

.image {
    display: flex;
    justify-content: center;
    align-items: center;
}

.price {
    font-size: 1.4rem;

    &::before {
        content: "$";
    }
}
